@media (min-width: @tablet-width) {
  .text-input {
    column-gap: 13px;
  }

  .text-input__label {
    font-size: 20px;
    font-style: normal;
    padding-top: 16px;
  }

  .text-input__input {
    font-size: 20px;
    line-height: 18px;
    height: 52px;
    padding: 0 15px 0 19px;
  }
}
